@import url(https://fonts.googleapis.com/css?family=Caveat|Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.imageHero{
  padding-top:6rem;
  padding-right: 10%;
}

.imageHero img{
  height: 400px;
}
.App-header{
  display: flex;
  flex-direction: row;
  margin-top: 8rem;
  margin-left:6rem;
  font-family: 'Work Sans', sans-serif;
}

.App-header h1{
  font-size:48px;
  margin-bottom: 4rem;
  font-family: 'Caveat', cursive;
  font-weight: 700;
  letter-spacing: 0.2rem;
  color:#212121;
}
.App-header div{
  margin-top:48px;
}

.intro{
  margin-bottom:0px;
}
.info{
  margin-bottom: 0px;
  max-width: 80%;
  line-height: 1.5;
}

.homeButton{
  margin-top:48px;
  font-size: 11px;
  color: white;
  background-color: black;
  padding:12px 16px;
  margin-right: 24px;
  border-radius: 24px;
  text-decoration: none;
}

